import InlineInput from '../components/input/InlineInput';
import styles from './Pages.module.css';

const InlineInputPage = () => {
  return (
    <div className={styles.pageContainer}>
      <main id='main-content' className={styles.mainContent}>
        <InlineInput />
        <hr className={styles.separator} />

        <h3 className={styles.heading}>
          Information om min Input-komponent med inlines
        </h3>

        <p className={styles.paragraph}>
          Den hr Input-komponenten r en anpassningsbar och tillgnglig lsning
          fr att hantera inmatning i formulr, inklusive etiketter, validering
          och felmeddelanden. Den kan anvndas fr alla typer av inmatningar,
          ssom text, e-post, nummer, telefonnummer och lsenord. Komponenten
          integrerar en smidig hantering av felmeddelanden och live-feedback,
          vilket gr den perfekt fr bde anvndarvnliga och tillgngliga
          formulr.
        </p>

        <hr className={styles.separator} />

        <h4 className={styles.subHeading}>Props fr Input-komponenten</h4>
        <p className={styles.paragraph}>Komponenten har fljande props:</p>
        <ul className={styles.list}>
          <li className={styles.listItem}>
            <div className={styles.itemHeader}>`type`:</div>
            <div className={styles.itemText}>
              Bestmmer vilken typ av inmatning (t.ex. text, lsenord, e-post).
              <br /> Den r obligatorisk.
            </div>
          </li>
          <li className={styles.listItem}>
            <div className={styles.itemHeader}>`label`:</div>
            <div className={styles.itemText}>
              Texten som visas som etikett fr fltet. <br /> Den r
              obligatorisk.
            </div>
          </li>
          <li className={styles.listItem}>
            <div className={styles.itemHeader}>`name`:</div>
            <div className={styles.itemText}>
              Unikt namn fr inputfltet. <br /> Den r obligatorisk.
            </div>
          </li>
          <li className={styles.listItem}>
            <div className={styles.itemHeader}>`value`:</div>
            <div className={styles.itemText}>
              Vrdet som visas i inputfltet. Kommer frn det aktuella
              tillstndet fr fltet (t.ex. frn `errorFields`). <br />
              Den r obligatorisk.
            </div>
          </li>
          <li className={styles.listItem}>
            <div className={styles.itemHeader}>`onChange`:</div>
            <div className={styles.itemText}>
              Funktion som anropas nr anvndaren ndrar innehllet i fltet
              (t.ex. `handleChange`). <br />
              Den r obligatorisk.
            </div>
          </li>
          <li className={styles.listItem}>
            <div className={styles.itemHeader}>`onBlur`:</div>
            <div className={styles.itemText}>
              Funktion som anropas nr fltet frlorar fokus (t.ex.
              `handleBlur`). <br />
              Den r obligatorisk.
            </div>
          </li>
          <li className={styles.listItem}>
            <div className={styles.itemHeader}>`placeholder`:</div>
            <div className={styles.itemText}>
              Texten som visas som platsinnehll i inputfltet nr det r tomt
              (t.ex. 'Skriv ditt namn hr'). <br />
              Den r valfri.
            </div>
          </li>
          <li className={styles.listItem}>
            <div className={styles.itemHeader}>`error`:</div>
            <div className={styles.itemText}>
              Eventuellt felmeddelande som r kopplat till fltet. <br />
              Den r valfri.
            </div>
          </li>
          <li className={styles.listItem}>
            <div className={styles.itemHeader}>`ref`:</div>
            <div className={styles.itemText}>
              En referens till inputfltet, anvnds fr att fokusera p fltet
              vid behov. <br />
              Den r valfri.
            </div>
          </li>
          <li className={styles.listItem}>
            <div className={styles.itemHeader}>`aria-describedby`:</div>
            <div className={styles.itemText}>
              ARIA-egenskap som kopplar ett felmeddelande (om det finns) till
              inputfltet fr att ge tillgnglighetsinformation. <br />
              Den r valfri.
            </div>
          </li>
          <li className={styles.listItem}>
            <div className={styles.itemHeader}>`required`:</div>
            <div className={styles.itemText}>
              Anger att fltet r obligatoriskt att fylla i innan formulret kan
              skickas. <br />
              Den r valfri men rekommenderas.
            </div>
          </li>
        </ul>

        <hr className={styles.separator} />

        <h4 className={styles.subHeading}>Tillgnglig design i fokus</h4>
        <ul className={styles.list}>
          <li className={styles.listItem}>
            <div className={styles.itemHeader}>
              Unik ID fr varje inputflt:
            </div>
            <div className={styles.itemText}>
              Varje inputflt fr ett unikt ID med hjlp av
              <code className={styles.inlineCode}>uuidv4()</code>, vilket gr
              att det kan kopplas till rtt etikett.
            </div>
          </li>
          <li className={styles.listItem}>
            <div className={styles.itemHeader}>
              Validering och felmeddelanden:
            </div>
            <div className={styles.itemText}>
              Om en inmatning inte r korrekt visas ett felmeddelande under
              fltet, och fltet fr en srskild CSS-klass fr att indikera att
              ngot r fel. Felmeddelandet r ocks tillgngligt fr skrmlsare
              genom
              <code className={styles.inlineCode}>aria-live="assertive"</code>.
            </div>
          </li>
          <li className={styles.listItem}>
            <div className={styles.itemHeader}>Skrmlsarstd:</div>
            <div className={styles.itemText}>
              Varje inputflt r kopplat till ett felmeddelande via
              <code className={styles.inlineCode}>aria-describedby</code> nr
              ett fel intrffar, vilket gr att skrmlsare kan lsa upp fltets
              tillstnd och eventuella fel.
            </div>
          </li>
          <li className={styles.listItem}>
            <div className={styles.itemHeader}>Live-feedback:</div>
            <div className={styles.itemText}>
              Fr att frbttra anvndarupplevelsen fr anvndare live-feedback
              om deras inmatning via
              <code className={styles.inlineCode}>aria-live="polite"</code>,
              vilket gr att skrmlsaren meddelar anvndaren om deras senaste
              inmatning utan att avbryta annan information.
            </div>
          </li>
          <li className={styles.listItem}>
            <div className={styles.itemHeader}>Fokus p fel:</div>
            <div className={styles.itemText}>
              Om ett flt inte r korrekt ifyllt, flyttas fokus till det frsta
              fltet med ett fel nr anvndaren frsker skicka formulret, s
              att de snabbt kan tgrda felet.
            </div>
          </li>
          <li className={styles.listItem}>
            <div className={styles.itemHeader}>Tillgnglig felhantering:</div>
            <div className={styles.itemText}>
              Felmeddelanden r kopplade till de relevanta inputflten och grs
              tillgngliga fr skrmlsare genom
              <code className={styles.inlineCode}>aria-describedby</code>, s
              att anvndaren kan hra varfr inmatningen inte r giltig.
            </div>
          </li>
        </ul>

        <hr className={styles.separator} />

        <h4 className={styles.subHeading}>Exempel p anvndning</h4>
        <pre className={styles.codeBlock}>
          <code>
            {`import React from 'react';
import Input from './Input';

const App = () => (
    <form>
      <Input
      type="text"
      label="Namn"
      name="name"
      required
      />
    </form>
  );
    
    export default AppInline;`}
          </code>
        </pre>
      </main>
    </div>
  );
};

export default InlineInputPage;
