import Buttons from '../components/buttons/Buttons';
import styles from './Pages.module.css';

const ButtonPage = () => {
  return (
    <div className={styles.pageContainer}>
      <main id='main-content' className={styles.mainContent}>
        <Buttons />
        <hr className={styles.separator} />

        <h3 className={styles.heading}>Information om min Buttons-komponent</h3>

        <p className={styles.paragraph}>
          Hr hittar du en flexibel och tillgnglig lsning fr att skapa
          knappar med olika stilar och funktioner. <br />
          Buttons-komponenten finns tre exempel p olika typer av login-knappar:
        </p>
        <ol className={styles.list}>
          <li className={styles.listItem}>
            <strong>Textknapp</strong>  En enkel knapp med endast text, perfekt
            fr tydliga och lttfrsteliga tgrder.
          </li>
          <li className={styles.listItem}>
            <strong>Ikonknapp</strong>  En knapp med endast en ikon, idealisk
            fr minimalistiska designlsningar.
          </li>
          <li className={styles.listItem}>
            <strong>Text- och ikonknapp</strong>  En kombination av text och
            ikon, perfekt fr att balansera tydlighet och design.
          </li>
        </ol>
        <p className={styles.paragraph}>
          Varje knapp r responsiv och fungerar med bde tangentbord och
          skrmlsare, s att alla anvndare kan anvnda dem utan problem. Nr
          du klickar p en knapp visas ett dynamiskt meddelande som bekrftar
          ditt val.
        </p>

        <hr className={styles.separator} />

        <h4 className={styles.subHeading}>Props fr Buttons-komponenten</h4>
        <p className={styles.paragraph}>
          Komponenten har <strong>inga props</strong> definierade, vilket
          innebr att den r fristende och inte tar emot ngra externa
          parametrar.
        </p>

        <hr className={styles.separator} />

        <h4 className={styles.subHeading}>Tillgnglig design i fokus</h4>
        <ul className={styles.list}>
          <li className={styles.listItem}>
            <div className={styles.itemHeader}>Skrmlsarstd:</div>
            <div className={styles.itemText}>
              Fr knappen med endast ikon har jag lagt till dold text fr
              skrmlsare via en
              <code className={styles.inlineCode}>&lt;span&gt;</code> med
              klassen <code className={styles.inlineCode}>visually-hidden</code>
              . Detta gr att skrmlsare lser upp samma text som finns p
              knappen, trots att den inte syns.
            </div>
          </li>
          <li className={styles.listItem}>
            <div className={styles.itemHeader}>
              Liveuppdatering av meddelanden:
            </div>
            <div className={styles.itemText}>
              Ett omrde med attributen
              <code className={styles.inlineCode}>aria-live="polite"</code> och
              <code className={styles.inlineCode}>aria-atomic="true"</code>
              anvnds fr att meddela anvndaren om vilken knapp som har
              klickats p. Detta gr att skrmlsare kan ge dynamisk
              terkoppling nr meddelandet ndras.
            </div>
          </li>
          <li className={styles.listItem}>
            <div className={styles.itemHeader}>Semantiska element:</div>
            <div className={styles.itemText}>
              Jag har anvnt mig av rubriktaggar och stycken fr en logisk
              struktur. Det hr gr att innehllet r ltt att frst bde fr
              anvndare och skrmlsare.
            </div>
          </li>
          <li className={styles.listItem}>
            <div className={styles.itemHeader}>Fokusindikatorer:</div>
            <div className={styles.itemText}>
              Har frbttrat tangentbordsnavigeringen med CSS-stil. Detta gr
              att anvndare kan se vilket element som r aktivt genom att lgga
              till en tydlig fokusindikator.
            </div>
          </li>
        </ul>

        <hr className={styles.separator} />

        <h4 className={styles.subHeading}>Exempel p anvndning</h4>
        <pre className={styles.codeBlock}>
          <code>
            {`import React from 'react';
import Buttons from './Buttons';

function App() {
  return (
    <div>
      <h1>Vlkommen till vr applikation!</h1>
      <Buttons />
    </div>
  );
}

export default App;`}
          </code>
        </pre>
      </main>
    </div>
  );
};

export default ButtonPage;
