import OnTopInput from '../components/input/OnTopInput';
import styles from './Pages.module.css';

const OnTopInputPage = () => {
  return (
    <div className={styles.pageContainer}>
      <main id='main-content' className={styles.mainContent}>
        <OnTopInput />
        <hr className={styles.separator} />

        <h3 className={styles.heading}>
          Information om min Input-komponent med onTop-meddelanden
        </h3>

        <p className={styles.paragraph}>
          Hr hittar du en flexibel och tillgnglig lsning fr att skapa
          anpassade formulrflt med etiketter, validering och responsiv design.
          Input-komponenten r utformad fr att stdja en rad olika flt,
          inklusive text, lsenord, e-post, telefonnummer och mer. Den erbjuder
          ocks funktioner fr felhantering och anvndarfeedback.
        </p>

        <hr className={styles.separator} />

        <h4 className={styles.subHeading}>Props fr Input-komponenten</h4>
        <p className={styles.paragraph}>Komponenten har fljande props:</p>
        <ul className={styles.list}>
          <li className={styles.listItem}>
            <div className={styles.itemHeader}>`type`:</div>
            <div className={styles.itemText}>
              Bestmmer typen av input (t.ex. text, lsenord, e-post,
              telefonnummer, etc.). Detta gr att komponenten kan anpassa sig
              efter olika behov.
            </div>
          </li>
          <li className={styles.listItem}>
            <div className={styles.itemHeader}>`label`:</div>
            <div className={styles.itemText}>
              Etiketten som visas fr inputfltet. Denna r obligatorisk och
              hjlper anvndare att frst vad fltet ska innehlla.
            </div>
          </li>
          <li className={styles.listItem}>
            <div className={styles.itemHeader}>`name`:</div>
            <div className={styles.itemText}>
              Namnet p inputfltet som anvnds fr att referera till fltet i
              formulret.
            </div>
          </li>
          <li className={styles.listItem}>
            <div className={styles.itemHeader}>`value`:</div>
            <div className={styles.itemText}>
              Vrdet i inputfltet. Detta r ndvndigt fr att hlla reda p
              anvndarens inmatning.
            </div>
          </li>
          <li className={styles.listItem}>
            <div className={styles.itemHeader}>`onChange`:</div>
            <div className={styles.itemText}>
              En funktion som anropas nr anvndaren ndrar vrdet i
              inputfltet.
            </div>
          </li>
          <li className={styles.listItem}>
            <div className={styles.itemHeader}>`onBlur`:</div>
            <div className={styles.itemText}>
              En funktion som anropas nr inputfltet frlorar fokus.
            </div>
          </li>
          <li className={styles.listItem}>
            <div className={styles.itemHeader}>`onKeyDown`:</div>
            <div className={styles.itemText}>
              En funktion som anropas nr en tangent trycks ner p
              tangentbordet.
            </div>
          </li>
          <li className={styles.listItem}>
            <div className={styles.itemHeader}>`placeholder`:</div>
            <div className={styles.itemText}>
              En text som visas i inputfltet nr det r tomt.
            </div>
          </li>
          <li className={styles.listItem}>
            <div className={styles.itemHeader}>`required`:</div>
            <div className={styles.itemText}>
              Anger om fltet r obligatoriskt att fylla i.
            </div>
          </li>
          <li className={styles.listItem}>
            <div className={styles.itemHeader}>`error`:</div>
            <div className={styles.itemText}>
              Ett felmeddelande som visas om validering misslyckas.
            </div>
          </li>
          <li className={styles.listItem}>
            <div className={styles.itemHeader}>`autoFocus`:</div>
            <div className={styles.itemText}>
              Om inputfltet ska f fokus vid sidladdning.
            </div>
          </li>
          <li className={styles.listItem}>
            <div className={styles.itemHeader}>`ref`:</div>
            <div className={styles.itemText}>
              En referens som gr att externa komponenter kan f tkomst till
              inputfltet.
            </div>
          </li>
        </ul>

        <hr className={styles.separator} />

        <h4 className={styles.subHeading}>Tillgnglig design i fokus</h4>
        <ul className={styles.list}>
          <li className={styles.listItem}>
            <div className={styles.itemHeader}>Skrmlsarstd:</div>
            <div className={styles.itemText}>
              Fr att skerstlla att alla anvndare, inklusive de som anvnder
              skrmlsare, kan frst fltet, har jag inkluderat en etikett (
              <code className={styles.inlineCode}>&lt;label&gt;</code>) som r
              kopplad till inputfltet via
              <code className={styles.inlineCode}>htmlFor</code>. Dessutom
              anvnds
              <code className={styles.inlineCode}>aria-live</code> och andra
              ARIA-attribut fr att skerstlla att anvndare med hjlpmedel fr
              rtt feedback i realtid.
            </div>
          </li>
          <li className={styles.listItem}>
            <div className={styles.itemHeader}>Felhantering och feedback:</div>
            <div className={styles.itemText}>
              Nr validering misslyckas visas ett felmeddelande under fltet,
              och fltet fr en srskild CSS-klass fr att indikera att ngot r
              fel. Felmeddelandet r ocks tillgngligt fr skrmlsare genom
              <code className={styles.inlineCode}>aria-live="assertive"</code>,
              vilket innebr att anvndaren omedelbart fr veta om ett fel utan
              att behva leta efter det.
            </div>
          </li>
          <li className={styles.listItem}>
            <div className={styles.itemHeader}>Semantiska element:</div>
            <div className={styles.itemText}>
              Jag anvnder mig av semantiska HTML-element, ssom
              <code className={styles.inlineCode}>&lt;label&gt;</code> och
              <code className={styles.inlineCode}>&lt;input&gt;</code>, fr att
              skerstlla att formulret r logiskt strukturerat och
              tillgngligt fr skrmlsare och anvndare. Semantiska element gr
              det enklare fr hjlpmedel att tolka och lsa upp innehllet fr
              anvndarna.
            </div>
          </li>
          <li className={styles.listItem}>
            <div className={styles.itemHeader}>Fokusindikatorer (via CSS):</div>
            <div className={styles.itemText}>
              Fr att frbttra tangentbordsnavigeringen kan du lgga till
              fokusindikatorer i din CSS-stil, vilket gr det lttare fr
              anvndare att se vilket flt som r aktivt. Detta hjlper
              anvndare som navigerar med tangentbordet att frst vilken del av
              formulret de fr nrvarande interagerar med.
            </div>
          </li>
        </ul>

        <hr className={styles.separator} />

        <h4 className={styles.subHeading}>Exempel p anvndning</h4>
        <p className={styles.paragraph}>
          Hr r ett exempel p hur du anvnder den i ett formulr:
        </p>

        <pre className={styles.codeBlock}>
          <code>
            {`import React, { useState } from 'react';
import Input from './Input';

function App() {
  const [formData, setFormData] = useState({ name: '', email: '' });

  const handleChange = (e, name) => {
    setFormData({ ...formData, [name]: e.target.value });
  };

  return (
    <form>
      <h1>Vnligen fyll i dina uppgifter</h1>

      <Input
        type="text"
        label="Namn"
        name="name"
        value={formData.name}
        onChange={(e) => handleChange(e, 'name')}
        required={true}
      />

      <Input
        type="email"
        label="E-post"
        name="email"
        value={formData.email}
        onChange={(e) => handleChange(e, 'email')}
        required={true}
      />

      <button type="submit">Skicka</button>
    </form>
  );
}

export default App;
           `}
          </code>
        </pre>
      </main>
    </div>
  );
};

export default OnTopInputPage;
